import React from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { InterestIconData } from "./data/IconData";

const Interests = () => {
  const interestIconData = InterestIconData.map((data) => (
    <div className="mb-3" key={data.name}>
      <FontAwesomeIcon icon={data.icon} className="mr-2" />
      {data.name}
    </div>
  ));

  return (
    <div className="all-center mt-5">
      <h2>Interests</h2>
      <div className="p-3 h5 font-weight-light">{interestIconData}</div>
    </div>
  );
};

export default Interests;
