import React, { useState } from "react";
import { Link } from "react-router-dom";
import { Navbar, Nav } from "react-bootstrap";

const Navigation = () => {
  const [expanded, setExpanded] = useState(false);

  return (
    <Navbar
      collapseOnSelect
      expand="lg"
      bg="dark"
      variant="dark"
      expanded={expanded}
    >
      <Navbar.Brand as={Link} to="/">
        <h3>Krishna Thorat</h3>
      </Navbar.Brand>
      <Navbar.Toggle
        onClick={() => setExpanded(expanded ? false : "expanded")}
        aria-controls="responsive-navbar-nav"
      />
      <Navbar.Collapse id="responsive-navbar-nav">
        <Nav className="mx-auto ">
          <Nav.Link
            as={Link}
            to="/"
            onClick={() => setExpanded(false)}
            className="lead px-5"
          >
            Home
          </Nav.Link>
          <Nav.Link
            as={Link}
            to="/education"
            onClick={() => setExpanded(false)}
            className="lead px-5"
          >
            Education
          </Nav.Link>
          <Nav.Link
            as={Link}
            to="/skills"
            onClick={() => setExpanded(false)}
            className="lead px-5"
          >
            Skills
          </Nav.Link>
          <Nav.Link
            as={Link}
            to="/interests"
            onClick={() => setExpanded(false)}
            className="lead px-5"
          >
            Interests
          </Nav.Link>
          <Nav.Link
            as={Link}
            to="/contact"
            onClick={() => setExpanded(false)}
            className="lead px-5"
          >
            Contact Me
          </Nav.Link>
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  );
};

export default Navigation;
