import React from "react";
import ReactTypingEffect from "react-typing-effect";
import noImg from "../img/noimg.png";
import profilePic from "../img/krishna.jpg";
import Tilt from "react-parallax-tilt";
import Icon from "./common/Icon";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPhoneAlt, faEnvelope } from "@fortawesome/free-solid-svg-icons";
import { SocialIconData } from "./data/IconData.js";
import "./Home.css";

const Home = () => {
  const socialIconData = SocialIconData.map((data) => (
    <Icon
      key={data.color}
      href={data.href}
      icon={data.icon}
      color={data.color}
    />
  ));

  return (
    <div className="all-center mt-5">
      <Tilt className="parallax-effect mb-5" perspective={500}>
        <div className="inner-element">
          <img
            src={profilePic || noImg}
            alt="profile_pic"
            className="profilepic rounded-circle"
          ></img>
        </div>
      </Tilt>

      <ReactTypingEffect
        className="typingeffect"
        text={["I am Krishna Girish Thorat", "I am a Web Developer"]}
        speed={100}
        eraseDelay={500}
      />

      <div className="info">
        <FontAwesomeIcon icon={faEnvelope} />
        <a href="mailto:krishnathorat007@gmail.com" className="px-2">
          krishnathorat007@gmail.com
        </a>
        <p style={{ textAlign: "center" }} className="pt-1">
          <FontAwesomeIcon icon={faPhoneAlt} /> +918550969625
        </p>
      </div>

      <div className="social-icons">{socialIconData}</div>
    </div>
  );
};

export default Home;
