import React from "react";
import { Form } from "react-bootstrap";

const Input = ({ label, type, placeholder, name, onChange, value, error }) => {
  return (
    <Form.Group>
      <Form.Label>
        <h5>{label}</h5>
      </Form.Label>
      <Form.Control
        type={type}
        placeholder={placeholder}
        name={name}
        onChange={onChange}
        value={value}
      />
      {error && <div className="alert alert-danger">{error}</div>}
    </Form.Group>
  );
};

export default Input;
