import React from "react";
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faHome } from "@fortawesome/free-solid-svg-icons";

const NotFound = () => {
  return (
    <div className="all-center mt-5">
      <p className="display-1 text-danger">Oops!</p>
      <p className="display-4 alert-danger">404 Not Found</p>
      <h3>Sorry, an error has occured, Requested page not found!</h3>
      <div className="mt-5">
        <Link to="/">
          <span className="btn btn-primary btn-lg">
            {" "}
            <FontAwesomeIcon icon={faHome} className="mr-2" />
            Go Home
          </span>
        </Link>
      </div>
    </div>
  );
};

export default NotFound;
