import React from "react";
import { Card } from "react-bootstrap";

const CardView = ({ title, year, board, institute, percent }) => {
  return (
    <Card
      border="light"
      style={{ width: "70vw" }}
      className="my-3 shadow-5 grow"
    >
      <Card.Body style={{ cursor: "pointer" }}>
        <Card.Title>
          {title}
          <small className="float-right blockquote-footer">
            Passing Year: {year}
          </small>
        </Card.Title>
        <Card.Text>
          <span className="mt-3">
            <strong>University/Board:</strong> {board}
          </span>
          <br />
          <span className="mt-2">
            <strong>School/College:</strong> {institute}
          </span>
          <br />
          <span className="my-2">
            <strong>Percentage:</strong> {percent}
          </span>
        </Card.Text>
      </Card.Body>
    </Card>
  );
};

export default CardView;
