import React, { useState } from "react";
import { Form } from "react-bootstrap";
import Input from "./common/Input";
import Joi from "joi-browser";
import { toast } from "react-toastify";
import firebaseConf from "./Firebase";

const Contact = ({ history }) => {
  const [user, setUser] = useState({
    name: "",
    email: "",
    phone: "",
    message: "",
  });
  const [error, setError] = useState({});

  const schema = {
    name: Joi.string().required().label("Name"),
    email: Joi.string().email().required().label("Email"),
    phone: Joi.string().length(10).regex(/^\d+$/).required().label("Phone"),
    message: Joi.string().required().min(5).max(50).label("Message"),
  };

  const validate = () => {
    const options = { abortEarly: false };
    const { error } = Joi.validate(user, schema, options);
    if (!error) return null;

    const errors = {};
    for (let item of error.details) errors[item.path[0]] = item.message;

    return errors;
  };

  const handleChange = ({ target: input }) => {
    const data = { ...user };
    data[input.name] = input.value;
    setUser(data);
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const error = validate();
    setError(error || {});
    if (error) return;

    const params = {
      name: user.name,
      email: user.email,
      phone: user.phone,
      message: user.message,
    };

    if (params.name && params.email && params.phone && params.message) {
      firebaseConf
        .database()
        .ref("contact")
        .push(params)
        .then(() => {
          toast.success("Sent successfully!", {
            position: "bottom-center",
            pauseOnHover: false,
          });
          history.push("/");
        })
        .catch(() => {
          toast.error("Something went wrong!", {
            position: "bottom-center",
            pauseOnHover: false,
          });
        });
    } else {
      toast.info("Please fill the form", {
        position: "bottom-center",
        pauseOnHover: false,
      });
    }
  };

  return (
    <div className="all-center mt-5">
      <h2>Contact</h2>
      <div className="w-50">
        <p className="text-info" style={{ textAlign: "center" }}>
          <strong>
            Want to get in touch? Fill out the form below to send me a message
            and I will get back to you as soon as possible!
          </strong>
        </p>
      </div>
      <Form className="form-style mt-3" onSubmit={handleSubmit}>
        <Input
          label="Name"
          type="text"
          placeholder="Jon Doe"
          name="name"
          onChange={handleChange}
          value={user.name}
          error={error.name}
        />
        <Input
          label="Email address"
          type="email"
          placeholder="name@example.com"
          name="email"
          onChange={handleChange}
          value={user.email}
          error={error.email}
        />
        <Input
          label="Phone Number"
          type="text"
          placeholder="9876543210"
          name="phone"
          onChange={handleChange}
          value={user.phone}
          error={error.phone}
        />

        <Form.Group>
          <Form.Label>
            <h5>Message</h5>
          </Form.Label>
          <Form.Control
            as="textarea"
            rows="4"
            name="message"
            value={user.message}
            onChange={handleChange}
          />
          {error.message && (
            <div className="alert alert-danger">{error.message}</div>
          )}
        </Form.Group>
        <div className="text-center">
          <button
            onClick={validate()}
            type="submit"
            className="btn btn-primary btn-lg mb-4"
          >
            Send
          </button>
        </div>
      </Form>
    </div>
  );
};

export default Contact;
